<template>
  <v-container fill-height fluid>
    <div class="header">
      <h3 class="headline" v-if="title">{{title}}</h3>
      <h4 class="caption white--text" v-if="titleSub">{{titleSub}}</h4>
    </div>
    <div class="contents">
      <v-form ref="form" v-model="valid" lazy-validation>
        <div class="v-input-group">
          <!--<input type="text" class="v-input" placeholder="email">
          <input type="text" class="v-input" placeholder="catagory">-->
          <v-text-field
            v-model="email"
            :rules="emailRules"
            label="E-mail"
            required
            append-icon="close"
          ></v-text-field>

          <v-select
            v-model="select"
            :items="items"
            :rules="[v => !!v || 'Item is required']"
            label=" "
            required
          ></v-select>
        </div>
        <!--<textarea type="text" class="v-input" placeholder="contents"></textarea>-->
        <v-textarea class="textarea" name="comment" label="" placeholder="  " hint></v-textarea>
        <v-btn color="accent" depressed @click="reset"></v-btn>
      </v-form>
    </div>
  </v-container>
</template>
<script>
// import store from "../store";
export default {
  data () {
    return {
      // inHeight: this.$store.state.inHeight,
      valid: true,
      title: ' ',
      titleSub: 'Easy From design to operation',
      article: '',
      name: '',
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 10) || 'Name must be less than 10 characters'
      ],
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+/.test(v) || 'E-mail must be valid'
      ],
      select: null,
      items: [
        ' ',
        ' ',
        ' ',
        'ATTO report',
        'in Studio'
      ],
      checkbox: false
    }
  },
  mounted () {
    // console.log(this.$store.state.isMobile)
  },
  methods: {
    validate () {
      if (this.$refs.form.validate()) {
        this.snackbar = true
      }
    },
    reset () {
      this.$refs.form.reset()
    }
    /* resetValidation() {
      this.$refs.form.resetValidation();
    } */
  }
}
</script>

<style lang="scss" scoped>
.container {
  background-color: #2f5aa8;
}
.headline {
  color: #fff;
}

@media (max-width: 750px) {
  ul {
    margin-top: 0;
  }
}
</style>
